/* reset */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  a {
    color: #2199df;
    text-decoration: unset;
  }
  
  
  ul {
    margin-left: 1.5rem;
    list-style: none;
  }
  
  button,
  input[type="submit"] {
    border: none;
    background: none;
    color: unset;
    cursor: pointer;
  }

  h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin-top: 1em;
  margin-bottom: 1em;
}

/* general */

body {
  font-family: "Montserrat", Montserrat;
  
}

#sheader{
  float:right;
  margin-bottom: 5%;
}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.Homebutton {
  font-weight: bold;
  background-color: #df4343;
  border: none;
  color: white;
  padding: 10px 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  max-width: 660px;
  padding-top: 10px;
}


/* header */

#header {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #82d325;
  
}
#header .title {
  
  margin: 0;
  padding: 0 3rem;
  color: #ffffff;
  
  
}
#header .auth-options {
  height: 100%;
  display: flex;
}

#header .auth-options button {
    padding: 0 1rem;
    background-color: #20b91b;
    color: #ffffff;
    font-size: 1.1rem;
}
  #header .auth-options button:hover {
    background-color: #be3a3a;
  }


/* form */

.form label,
.form input {
  display: block;
}
.form input {
  margin-bottom: 1rem;
}
.form label {
  margin-bottom: 0.2rem;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="password"] {
  width: 50%;
  padding: 0.3rem;
  border: 1px solid #c2c2c2;
  border-radius: 8px;
  background-color: #f0f0f0;
  font-size: 1.1rem;
}
.form input:focus {
  border: 1px solid #50a4e9;
  box-shadow: inset 0px 0px 5px rgba(38, 142, 228, 0.5);
  outline: none;
}
.form input[type="submit"] {
  padding: 0.3rem 0.5rem;
  border: 1px solid #6aa051;
  border-radius: 8px;
  background-color: #c8ecb7;
  font-size: 1.1rem;
}

/* todo list */

.todo-list ul {
  list-style: disc;
  cursor: pointer;
}

.todo-list li:hover {
  text-decoration: line-through;
}

/* error notice */

.error-notice {
  margin: 1rem 0;
  padding: 0.5rem;
  border: 1px solid #e07c7c;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8d6d6;
}
.error-notice button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #df4343;
  color: #ffffff;
}

.as1 {
  width: 25%;
  padding-right: 15px;
  margin-right: 15px;
  float: left;
  font-style: italic;
  background-color: lightgray;
}

#chart {
  height: 100px;
  width: 100px;
}




.welcome{
  margin: 10px;
  color: white;
  float: left;
}


  /* media queries */

@media screen and (max-width: 550px) {
    #header .title {
      font-size: 1.2rem;
    }
    #header .auth-options button {
      padding: 0 0.5rem;
      font-size: 0.9rem;
    }
  }